<template>
	<div>
		<div class="tip">
			<p>温馨提醒：一个手机号码只能注册一次（既一个供应商只能拥有一个值值宝）</p>
		</div>
		<el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="120px" style="text-align: left;">
			<el-form-item label="身份证" prop="idCard">
				<el-input v-model="ruleForm.idCard" clearable maxlength="18"></el-input>
			</el-form-item>
			<el-form-item label="联系电话" prop="tel">
				<el-input v-model="ruleForm.tel" clearable maxlength="11"></el-input>
			</el-form-item>
			<el-form-item label="短信验证码" prop="telcode">
				<el-input v-model="ruleForm.telcode" clearable maxlength="6"></el-input>
				<el-button @click.native.prevent="getTelCode" type="primary" >获取</el-button>
			</el-form-item>
			<el-form-item label="密码" prop="pwd">
				<el-input v-model="ruleForm.pwd" clearable></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click.native.prevent="submitForm" :loading="loading">提交</el-button>
				<el-button  @click.native.prevent="returnZhizhibao" >返回</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
  import add from '../../../../assets/js/nav/zzbao/child/add.js'
  export default add
</script>

<style scoped lang="scss" >
@import '../../../../assets/css/nav/zzbao/child/add.scss'
</style>
<style>
.el-table th{
    background-color: #eef1f6;
}
</style>


