 export default {
      data() {
      	let idCardReg=(rule,value,callback)=>{
	     	this.ruleForm.idCard=value;
	      	if(this.ruleForm.idCard!=""){
	      		const reg=/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
	      		if(reg.test(value)){
	      			return callback();
	      		}else{
	      			return callback(new Error('身份证号码为15位或者18位，15位时全为数字，18位前17位为数字，最后一位是校验位，可能为数字或字符X'));
	      		}
	      	}else{
	      		return callback(new Error('请输入身份证'))
	      	}
	    }
      	let telReg=(rule,value,callback)=>{
	     	this.ruleForm.tel=value;
	      	if(this.ruleForm.tel!=""){
	      		const reg=/^1(3|4|5|6|7|8)\d{9}$/;
	      		if(reg.test(value)){
	      			return callback();
	      		}else{
	      			return callback(new Error('手机号码格式有误'));
	      		}
	      	}else{
	      		return callback(new Error('请输入手机号码'))
	      	}
	    } 
      	let telcodeReg=(rule,value,callback)=>{
	     	this.ruleForm.telcode=value;
	      	if(this.ruleForm.telcode!=""){
	      		if(this.ruleForm.telcode.length==6){
	      			return callback();
	      		}else{
	      			return callback(new Error('验证码为6位数'));
	      		}
	      	}else{
	      		return callback(new Error('请输入验证码'))
	      	}
	    } 
        return {
          ruleForm: {
            idCard: '',
            tel: '',
            telcode:'',
            pwd: '',
          },
          loading:false,
          rules:{
				idCard:[
					{
	      			trigger:'blur',
	      			validator:idCardReg,
					}
					],
				tel:[
					{
					trigger:'blur',
	      			validator:telReg,
					}
					],
				telcode:[
					{
					trigger:'blur',
	      			validator:telcodeReg,
					}
				],
				pwd:[
					{
					required:true,
	      			message:'密码不能为空',
	      			trigger:'blur'
					}
					],
			}
        }
      },
      methods: {
			submitForm() {
				this.$refs.ruleForm.validate((valid)=>{
					if(valid){
						 this.$api.put('gyswallet/createGysWalletInfo',{
						 	'idCard':this.ruleForm.idCard,
						 	'tel':this.ruleForm.tel,
						 	'telcode':this.ruleForm.telcode,
						 	'password':this.ruleForm.pwd
						 },localStorage.token, success => {
						 	 this.$message({
					            type: 'success',
					            message: success.msg
					       });
				 			this.$router.push({ path: '/nav/zzbao/zzbao' });
						  },
						 failure=>{
						   this.$message({
					            type: 'error',
					            message: failure.msg
					       });
						 })
						 
					}
				})
			},
			getTelCode(){
				if(this.ruleForm.tel!=""){
					 this.$api.post('gyswallet/createGysWalletSendEmail/'+this.ruleForm.tel,{},localStorage.token, success => {
				 			this.$message({
					            type: 'success',
					            message: success.msg
					          });
						  },
						 failure=>{
						   this.$message({
					            type: 'error',
					            message: '获取短信验证码失败'
					      	});
						 })
				}else{
					this.$message({
		            type: 'waring',
		            message: '手机号码不能为空'
		          });
				}
				
			},
			returnZhizhibao(){
				this.$router.push({ path: '/nav/zzbao/zzbao' });
				 this.showCard=false;
			}
			}

    }


